:global html,
:global body {
  font-family: 'Montserrat', Helvetica, Arial, sans-serif;
  font-size: 16px;
  height: 100%;
  min-height: 100%;
}
@media screen and (max-width: 768px) {
  :global html,
  :global body {
    font-size: 10px;
  }
}
:global body {
  color: #c9c9c9;
  background-color: #404040;
}
:global #oz-root {
  height: 100%;
}
