.logo {
  position: absolute;
  bottom: 4rem;
  left: 4rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: stretch;
  text-decoration: none;
}
.logo:focus {
  outline: none;
}
.logo__name,
.logo__position {
  font-family: 'Fira Code';
  display: block;
}
.logo__name {
  position: relative;
  left: 3.75rem;
  font-size: 4rem;
  font-weight: bold;
  white-space: nowrap;
}
.logo__name__regular {
  color: #c9c9c9;
}
.logo__name__accent {
  color: #00d2d6;
}
.logo__position {
  font-size: 2rem;
  white-space: nowrap;
  color: #8c8c8c;
}
